<template>
  <div class="app-container">
    <van-nav-bar
      class="bar"
      fixed
      placeholder
      title="报告信息"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-tabs v-model="active" @click="tabClick" background="#f1f1f1" color="#1092D4" title-active-color="#1092D4">
      <van-tab title="基本信息">
        <div class="flex-column">
          <div class="res-box pat-msg">
          <van-row>
            <van-col span="12">
              <van-cell title="姓名" :value="patient.realName" />
            </van-col>
            <van-col span="12">
              <van-cell title="性别" :value="patient.gender?'男':'女'" />
            </van-col>
            <van-col span="12">
              <van-cell title="年龄" :value="patient.age" />
            </van-col>
            <van-col span="12">
              <van-cell title="民族" :value="patient.nation" />
            </van-col>
            <van-col span="12">
              <van-cell title="科室" :value="patient.fromDeptName" />
            </van-col>
            <van-col span="12">
              <van-cell title="病历号" :value="patient.beHospitalizedNumber" />
            </van-col>
          </van-row>
        </div>

        <div class="res-content">
          <p class="title">主要症状:</p>
          <div class="label" v-for="(item,index) in patient.symptom" :key="index">{{item}}</div>
        </div>
        </div>
        
      </van-tab>
      <van-tab title="筛查结果概况">
        <div class="flex-column">
          <div class="res-box">
            <ve-radar height="250px" :data="chartData" :settings="chartSettings" :extend="extend"></ve-radar>
          </div>
          <div class="res-content">
              <div class="label" v-for="(item,index) in explanation" :key="index">
                 <span>{{item}}</span>
              </div>
              <div class="conclusion">{{conclusion}}</div>
          </div>
        </div>
      </van-tab>
      <van-tab title="自主神经检查">
        <div class="res-box img">
          <van-image :src="base64" v-if="base64!==''"></van-image>
          <van-empty description="HRV暂时没有数据" v-else />
        </div>
      </van-tab>
      <van-tab title="量表评估">
        <div class="res-box scale">
            <scale-assess :data="scaleList"></scale-assess>
        </div>
      </van-tab>
    </van-tabs>
    <van-overlay :show="show" @click="show = false" >
      <div class="wrapper">
        <van-loading  class="block" size="50px" vertical type="spinner"> 加载中...</van-loading>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { medicalReadRecord } from "@/api/patient";
import { getHRV } from "@/api/HRV";
import { getReportAssess,scaleResult } from "@/api/report";
import scaleAssess from "@/views/scale/scaleAssess";
import {mapGetters} from 'vuex'
export default {
  name: "home",
  components: {
    scaleAssess
  },
  data() {
    return {
      base64:"",
      active:0,
      patient:{},
      scaleList:[],
      medicalRecordId:"",
      show:true,
       extend: {
        legend: {
          show: false
        },
        radar: {
          radius: 60,
          indicator: [
            { name: "抑郁", max: 27 },
            { name: "躯体症状", max: 30 },
            { name: "焦虑", max: 21 },
            { name: "惊恐", max: 10 },
            { name: "社会功能", max: 3 },
          ]
        },
        series: {
          label: {
            normal: {
              show: true
            }
          }
        }
      },
      explanation: [],
      conclusion:"",
      chartSettings: {
        label: {
          positon: "top"
        }
      },
      chartData: {
        columns: ["日期"],
        rows: [{ "日期": "1", "抑郁": 0, "躯体症状": 0, "焦虑": 0, "惊恐": 0 ,"社会功能":0}]
      }
    };
  },
   computed:{
      ...mapGetters([             //步骤二，对象扩展运算符方式
        "info"
      ])
    },
  mounted() {
    console.log(this.info)
     this.medicalRecordId = this.$route.query.id;
    this.getMedicalReadRecordData();
  },
  methods: {
    tabClick(){
      let active=this.active;
      switch(active){
        case 0:
         this.getMedicalReadRecordData();
          break;
          case 1:
            this.getReportAssessData();
          break;
          case 2:
            this.getHRVData();
          break;
          case 3:
            this.getScaleResult();
          break;
      }
    },
    getMedicalReadRecordData(){
      this.show=true;
       medicalReadRecord(this.medicalRecordId).then(res => {
         this.show=false;
       if(res.code==200){
         console.log(1111111)
         this.patient=res.dataList[0];
       }
    });
    },
    getHRVData(){
      this.show=true;
      getHRV(this.medicalRecordId).then(res=>{
         this.show=false;
        if(res.code==200){
          this.data=res.dataList[0];
          this.base64="data:image/png;base64,"+this.data.resultImageUrl;
        } 
      })
    },
    getReportAssessData(){
      this.show=true;
      getReportAssess(this.medicalRecordId).then(res => {
         this.show=false;
      if (res.code == 200) {
        let data = res.dataList[0];
        this.conclusion=data.conclusion;
        this.explanation = data.explanation;
        let chartData = JSON.parse(data.chartData);
        for (let item of chartData) {
          this.chartData.columns.push(item.name);
          this.chartData.rows[0][item.name] = item.score;
        }
      }
    });
    },
    onClickLeft() {
       this.$router.push("/history");
    },
     getScaleResult() {
       this.show=true;
      return scaleResult(this.medicalRecordId).then(res => {
         this.show=false;
        let data = res.dataList;
        this.scaleList = data;
      });
    },
  }
};
</script>
<style lang="less" scoped>
.flex-column {
  height: calc(100vh - 90px);
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  justify-content: space-between;
}
.app-container {
  height: 100vh;
  background: #1092d4;
  /deep/.van-nav-bar {
    background-color: #1092d4;
    color: #fff;
    .van-icon {
      color: #fff;
    }
    .van-nav-bar__title {
      color: #fff;
    }
  }
  /deep/ .van-nav-bar__text {
    color: #fff;
  }
}
.res-box {
  margin: 10px;
  background: #fff;
  &.img{
    height: calc(100vh - 110px);
  }
  &.scale{
    height:calc(100vh - 110px);
    overflow: auto;
  }
}
.res-content {
  margin: 10px;
  padding: 10px;
  background: #fff;
  flex: 1;
  .title{
    font-size: 16px;
    color: #222;
    font-weight: bold;
  }
  .label{
    font-size: 14px;
    line-height: 35px;
    color: #666;
  }
  .conclusion{
    line-height: 30px;
    font-size: 13px;
    color: #222;
  }
  
}

.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  .block {
    width: 80px;
    height: 80px;
    padding: 10px 20px;
    background-color: #fff;
    border-radius: 10px;
  }
</style>



